@font-face {
  font-family: Inter-Bold;
  src: url(/fonts/Inter/Inter-Bold.woff2);
}

@font-face {
  font-family: Inter-Medium;
  src: url(/fonts/Inter/Inter-Medium.woff2);
}

@font-face {
  font-family: Inter-Regular;
  src: url(/fonts/Inter/Inter-Regular.woff2);
}

@font-face {
  font-family: Inter-SemiBoldItalic;
  src: url(/fonts/Inter/Inter-SemiBoldItalic.woff2);
}

@font-face {
  font-family: Inter-SemiBold;
  src: url(/fonts/Inter/Inter-SemiBold.woff2);
}

body {
  font-family: Inter, sans-serif;
  line-height: 1.4;
  color: $color-grey-light;
  -webkit-font-feature-settings: 'cv05' 1;
  font-feature-settings: 'cv05' 1;
}

.heading-primary {
  font-size: 4rem;
  font-weight: 700;
  line-height: 1.2;
  margin: 1rem 0;

  &:first-child {
    margin-top: 0;
  }

  @include respond(tab-small) {
    font-size: 2rem;
  }
}

.heading-secondary {
  font-size: 2.8rem;
  font-weight: 700;
  line-height: 1.3;
  margin: 1rem 0;

  &:first-child {
    margin-top: 0;
  }

  @include respond(tab-small) {
    font-size: 1.7rem;
  }
}

.heading-tertiary {
  font-size: 1.3rem;
  font-weight: 700;
  line-height: 1.3;
  margin: 0;

  &:first-child {
    margin-top: 0;
  }

  @include respond(tab-small) {
    font-size: 1.15rem;
  }
}

.paragraph {
  font-size: $default-font-size;
  margin: 1rem 0;
}
